 @import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital@0;1&display=swap");

  
  ul {
    list-style: none;
  }
  
  a {
    font-family: 'Poppins', sans-serif !important;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
    color: white;
    font-size: 15px;
    font-weight: 500;
  }
  .navLink:hover{
    color:#39a6e8 !important ;
  }
 
  
  /* navbar */
  .navbar {
    background-color:#19212C;
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 70px;
    width: 100%;
  }
  
  .btn{
    font-family: 'Poppins', sans-serif;
    font-family: 'Roboto', sans-serif;
    padding: 10px 12px;
    background-color: transparent;
    border: #39a6e8  1px solid;
    border-radius: 50px;
    color: #39a6e8 ;
  }
  .btn:hover{
    background-color:#39a6e8 ;
    color: white;
  }
  /* logo  */
  .logo {
    font-family: 'Poppins', sans-serif;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
  }
  .logo:hover{
    color:#39a6e8 !important ;
  }
  
  /* ul  */
  .navMenu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
    margin: 0;
    padding: 0;
  }
  
  /* media queries */
  .hamburger {
    display: none;
  }

  @media screen and (max-width: 1229px){
    
    .btn{
     display: none;
    }
    /* hamburger */
    .hamburger {
      display: block;
      cursor: pointer;
    }
  
    .bar {
          background-color: #ffffff;
          width: 20px;
          height: 3px;
          display: block;
          margin: 5px;
          -webkit-transition: 0.3s ease-in-out;
          transition: 0.3s ease-in-out;
      }
  
    /* adding active class to the bars  */
    .hamburger.active .bar:nth-child(2){
      opacity: 0;
    }
    .hamburger.active .bar:nth-child(1){
      transform: translateY(8px) rotate(45deg);
    }
    .hamburger.active .bar:nth-child(3){
      transform: translateY(-8px) rotate(-45deg);
    }
  
  
    /* navMenu */
  
    .navMenu{
      position: absolute;
      flex-direction: column;
      gap: 0;
      top: 70px;
      left: -100%;
      text-align: start;
      width: 100%;
      transition: 0.7s ease-in-out;
      background-color: #19212C;
      padding: 10px;
    }
  
    .navMenu.active{
      left: 0;
    }
  
    .navMenu li {
      margin: 16px 0;
    }
  }


  